import React, {Component} from "react";
import InputWithFloatingLabel from "../../magnum2/components/InputWithFloatingLabel";

export default class BillingAccountSearchComponent extends Component {

    constructor(props) {
        super(props);
        this.state = {msisdn: ""};
    }

    inputLabel() {
        return "Numer MSISDN";
        return "Numer MSISDN lub konta";//to use after resolving NOR-184581
    }

    onMsisdnFieldChange(valiObj) {
        this.setState({msisdn: valiObj.value});
    }

    validateAccountCode() {
        let input = this.getMsisdnInputVal();
        //make validation for accountCode temporarly unavaliable
        return false;
    }

    validateMsisdn() {
        console.log("VALIDATE MSISDN FIELD");
        let msisdn = this.getMsisdnInputVal();
        if (!msisdn)
            return true;
        console.log(this.getMsisdnInputVal());
        if ((msisdn.length == 9 && jQuery.isNumeric(msisdn)))
            return true;
        return false;
    }

    getMsisdnInputVal() {
        return this.state && this.state.msisdn && this.state.msisdn.replace(/-/g, "");
    }

    findByInput(e) {
        e.stopPropagation();
        if (this.validateMsisdn()) {
            this.props.getAccount(this.state.msisdn, false);
        } else if (this.validateAccountCode()) {
            this.props.getAccountByCode(this.state.msisdn);

        }
    }

    onKeyUp(e) {
        if (e.keyCode == 13) {
            this.findByInput(e);
        }
    }

    isInputFieldValid() {
        return this.validateMsisdn();
        return this.validateMsisdn() || this.validateAccountCode();//to use after resolving NOR-184581
    }

    render() {
        return (

            <div className="u-padding-all-m">
                <p>Klient ma zbyt wiele kart SIM, żeby móc pobrać jego listę kont</p>
                <div className="l-row u-spacing-top-m">
                    <div className="l-col l-col-small-12 l-col-medium-8 l-col-8 u-small-padding">
                        <InputWithFloatingLabel
                            id="msisdn"
                            value={this.state.msisdn}
                            required={true}
                            onChange={this.onMsisdnFieldChange.bind(this)}
                            placeholder={this.inputLabel()}
                            focusOnMount={true}

                            showValidation={true}
                            showErrors={true}
                            valid={this.isInputFieldValid()}
                            errors={!(this.isInputFieldValid())}

                            onKeyUp={this.onKeyUp.bind(this)}
                            overrideClass={"o-floating-label opl-floating-label-line opl-floating-label-line--white opl-autocomplete u-w-100"}
                        />

                    </div>
                    <div className="l-col l-col-small-12 l-col-medium-4 l-col-4 ">
                        <button id="sms" type="submit" onClick={this.findByInput.bind(this)}
                                className="o-btn opl-btn opl-btn--secondary u-w-100">Wyszukaj
                        </button>
                    </div>

                </div>
            </div>

        )
    }
}
